<template>
    <f7-page class="guide">
        <h1>GUA DE REGISTRO Y ACCESO</h1>

        <f7-block>
            <h2>
                <f7-icon material="label" size="28"></f7-icon>&nbsp;&nbsp;REGISTRO
            </h2>
            <p>Si eres un nuevo usuario de Patbook debers registrarte antes de poder utilizar la aplicacin.</p>
            <p>En la siguiente pantalla accede a NUEVO USUARIO. Por ahora slo tendrs que proporcionar un nombre de
                usuario de tu eleccin, una cuenta de correo electrnico a la que tengas acceso y una contrasea,
                tambin de tu eleccin (NOTA: la contrasea ha de ser nica para Patbook, NO uses la de tu cuenta de
                correo y ha de tener, como mnimo 6 caracteres).</p>
            <p>Toca sobre REGISTRAR y habrs finalizado el proceso de registro.</p>
            <p>Por razones de seguridad y privacidad una vez registrado como usuario tambin se te pedir que registres
                CADA DISPOSITIVO desde el que vayas a acceder a tu cuenta, as, aunque alguien obtenga maliciosamente
                tus datos de acceso nunca podr acceder a tu cuenta si lo intenta desde un dispositivo que t no hayas
                registrado previamente.</p>
            <p>Para ello, nada ms registrarte, te enviaremos un email con un cdigo y la aplicacin te conducir a la
                pantalla siguiente. Escribe all dicho cdigo y toca sobre REGISTRAR ESTE DISPOSITIVO.</p>
            <p>Acceders directamente a la pantalla principal de Patbook y podrs comenzar a usar la aplicacin.</p>
            <p><strong>NO</strong> tendrs que volver a registrar un dispositivo que ya hayas registrado anteriormente,
                ni te volveremos a pedir el cdigo. El sistema guardar la informacin y te permitir acceder slo con
                tu email y contrasea.</p>
            <p><strong>IMPORTANTE: </strong>Si cerraste la aplicacin tras haberte registrado como usuario, pero antes
                de registrar el dispositivo, an puedes completar el proceso la prxima vez que accedas a la aplicacin.
                En la pantalla de ACCESO toca sobre REGISTRAR DISPOSITIVO y sers conducido a una nueva pantalla donde
                se te pedir que introduzcas el email y la contrasea con que te registraste junto con el cdigo que te
                enviamos a tu cuenta de correo.</p>
            <p><strong>NO</strong> tienes que volver a registrarte como usuario cada vez que registres un dispositivo.
            </p>
            <h2>
                <f7-icon material="label" size="28"></f7-icon>&nbsp;&nbsp;ACCESO
            </h2>
            <p>En la pantalla de ACCESO introduce tu email y contrasea.</p>
            <p>Si es la primera vez que usas un dispositivo para acceder a tu cuenta la aplicacin te dirigir a una
                pantalla que te advierte de este hecho. Introduce la cuenta de correo electrnico con que te registraste
                y te enviaremos un email con un cdigo. En la pantalla siguiente escribe dicho cdigo y toca sobre
                REGISTRAR ESTE DISPOSITIVO.</p>
            <p><strong>IMPORTANTE: </strong>Si cerraste la aplicacin antes de registrar el dispositivo, an puedes
                completar el proceso la prxima vez que accedas a la aplicacin. En la pantalla de ACCESO toca sobre
                REGISTRAR DISPOSITIVO y sers conducido a una nueva pantalla donde se te pedir que introduzcas el email
                y la contrasea con que te registraste junto con el cdigo que te enviamos a tu cuenta de correo.</p>
            <h2>
                <f7-icon material="label" size="28"></f7-icon>&nbsp;&nbsp;RECUPERAR LA CONTRASEA
            </h2>
            <p>Si no recuerdas tu contrasea en la pantalla de ACCESO toca sobre HE OLVIDADO MI CONTRASEA.</p>
            <p>A continuacin introduce la cuenta de correo electrnico con que te registraste, toca sobre ENVIAR y te
                enviaremos un email con un cdigo.</p>
            <p>En la siguiente pantalla introduce dicho cdigo y una nueva contrasea de, como mnimo, 6 caracteres.
                Toca sobre CREAR NUEVA CONTRASEA.</p>
            <p>Si todo ha ido bien sers devuelto a la pantalla de ACCESO y podrs acceder con tu nueva contrasea.</p>

            <br>
            <f7-button big fill round @click="proceed">CONTINUAR</f7-button>

            <f7-list simple-list>
                <f7-list-item>
                    <span>No volver a mostrar esta pgina *</span>
                    <f7-toggle
                            :checked="dontShowAnymore"
                            @change="dontShowAnymore = $event.target.checked"
                    ></f7-toggle>
                </f7-list-item>
                <p>
                    <small>* Si deseas ver de nuevo esta gua puedes hallarla ms adelante en el men de Ms Opciones"
                    </small>
                </p>
            </f7-list>
        </f7-block>

    </f7-page>
</template>

<script>
    import F7Toggle from "framework7-vue/src/components/toggle";

    export default {
        name: 'UseGuide',
        data() {
            return {
                dontShowAnymore: false
            };
        },
        methods: {
            proceed() {
                if (this.dontShowAnymore === true) {
                    localStorage.patbookDontShowTheGuide = 'true';
                }
                this.$f7router.navigate('/login');
            }
        }
    };
</script>

<style scoped>
    .guide {
        background-color: #1ABAD4;
        color: #ffffff;
    }

    .guide h1 {
        text-align: center;
    }

    .guide h2 {
        line-height: 28px;
    }

    .button-fill {
        border: 1px solid #ffffff;
    }
</style>


